.nav {}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__link {
  display: flex;
  align-items: center;
  padding: 7px 10px 9px;
  font-size: 14px;
  text-decoration: none;
  color: #333;
  border-radius: 5px;
}

.nav__link:hover,
.nav__link_active {
  background-color: #fafafa;
}

.nav__icon {
  display: flex;
  justify-content: center;
  width: 20px;
  margin-right: 10px;
  font-size: 16px;
  opacity: .7;
}

.nav__link:hover .nav__icon,
.nav__link_active .nav__icon {
  opacity: 1;
}

.nav__count {
  margin-left: auto;
}