body {
  margin: 0;
  font: 16px 'Segoe UI', Arial, Helvetica, sans-serif;
  color: #333;
  background-color: #fff;
}

.app {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
}

.header {}

.header__title {
  margin: 0 0 20px;
}

.columns {
  display: flex;
  align-items: flex-start;
  gap: 20px;
  flex-wrap: wrap;
}

@media (max-width: 991px) {
  .columns {
    display: block;
  }
}

.sidebar {
  width: 25%;
  min-width: 200px;
  padding: 10px;
  background-color: #f4f4f4;
  border-radius: 5px;
  box-sizing: border-box;
}

@media (max-width: 991px) {
  .sidebar {
    width: 100%;
    margin-bottom: 20px;
  }
}

.main {
  flex: 1;
  padding: 0 0 10px;
}

.main__title {
  margin-bottom: 10px;
  font-size: 18px;
}