.add-item {
  margin: 0 0 10px;
}

.add-item__form {
  display: flex;
}

.add-item__field {
  margin-right: -1px;
  flex: 1;
}

.add-item__input {
  position: relative;
  width: 100%;
  height: 37px;
  padding: 0 10px;
  background-color: #fff;
  border: 1px solid #e5e5e5;
  border-right: 0;
  border-radius: 5px 0 0 5px;
  box-sizing: border-box;
}

.add-item__favorites {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  border: 1px solid #e5e5e5;
  border-left: 0;
  border-right: 0;
  cursor: pointer;
}

.add-item__favorites input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  opacity: 0;
  overflow: hidden;
  appearance: none;
}

.add-item__favorites span {
  position: relative;
  top: -1px;
}

.add-item__favorites span::before {
  content: '\f005';
  font-weight: 400;
  font-family: 'Font Awesome 5 Free';
}

.add-item__favorites input:checked + span::before {
  font-weight: 900;
}

.add-item__submit {
  padding: 0 10px;
  background-color: #f4f4f4;;
  border: 1px solid #e5e5e5;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
}

.add-item__submit:hover {
  background-color: #fafafa;
}