.todoitem {
  height: 40px;
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: -1px;
  border: 1px solid #e5e5e5;
  box-sizing: border-box;
}

.todoitem:first-child {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.todoitem:last-child {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.todoitem__checkbox {
  width: 37px;
  height: 100%;
  margin: 0;
  padding: 0;
  background: transparent;
  border: 0;
  cursor: pointer;
}

.todoitem:first-child .todoitem__checkbox {
  border-radius: 5px 0 0 0;
}

.todoitem:last-child .todoitem__checkbox {
  border-radius: 0 0 0 5px;
}

.todoitem__checkbox::before {
  content: '\f0c8';
  font-size: 16px;
  font-family: 'Font Awesome 5 Free';
  color: inherit;
}

.todoitem__text {
  flex: 1;
  max-width: 500px;
  position: relative;
  top: -1px;
  font-size: 16px;
  text-overflow: ellipsis;
  overflow: hidden;
}

.todoitem__favorites {
  width: 37px;
  height: 100%;
  margin-left: auto;
  padding: 0;
  font-family: inherit;
  background: transparent;
  border: 0;
  cursor: pointer;
}

.todoitem__favorites::before {
  content: '\f005';
  font-weight: 400;
  font-size: 16px;
  font-family: 'Font Awesome 5 Free';
  color: inherit;
}

.todoitem__favorites:hover::before {
  font-weight: 900;
}

.todoitem__delete {
  width: 37px;
  height: 100%;
  padding: 0;
  background: transparent;
  border: 0;
  opacity: 0;
  cursor: pointer;
}

@media (max-width: 991px) {
  .todoitem__delete {
    opacity: .4;
  }
}

.todoitem__delete::before {
  content: '\f1f8';
  font-weight: 900;
  font-family: 'Font Awesome 5 Free';
  color: inherit;
}

.todoitem:hover .todoitem__delete {
  opacity: .4;
}

/* Modifier  */
.todoitem__checkbox_isChecked {
  color: #3e69e4;
}

.todoitem__checkbox_isChecked::before {
  content: '\f14a';
  font-weight: 900;
}

.todoitem__favorites_isChecked::before {
  font-weight: 900;
}